.todoListMain .header input {
     background-color:  rgb(231, 246, 248);;
     border: 0;
     width: 200px;
     height: 50px;
     padding: 0 20px;
     margin: 20px;
     font-size: 18px;
     border-radius: 10px;      
}

.todoListMain .header button {
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color: #f35c89;
    font-size: 18px;
    font-weight: bolder;
    color: #fff;  
}

.todoListMain input:focus {
    outline: none;
}

.todoListMain button:focus {
    outline: none;
}

.todoListMain .header button:hover {
    background-color: #e68aa4;
    border: #e68aa4;
    cursor: pointer;
}

 .todoListMain .theList li {
     background-color: #c9c269;
     margin-bottom: 15px;
     border-radius: 5px;
     list-style: none;
     padding: 15px;    

     transition:  background-color .2s ease-out;
}

.todoListMain .theList li:hover {
    background-color:  #dad59a ;
    cursor: pointer;

}

ul.theList {
    padding: 4;
}


